.container {
    z-index: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 1rem 1rem;
    
    background-color: #383a45;
    background-image: linear-gradient(135deg, #4f5261 0%, #383a45 50%);
    border-bottom: 1px solid #e7edf2;
}

.container .logoLink {
    margin-bottom: 2rem;
}

.logoImage {
    width: 100px;
    height: auto;
}